.main-cases {

  .container {
    @include laptop {
      padding: 0;
      max-width: none;
    }
  }

  &.section {
    padding-top: 80px;
    padding-bottom: 0;

    @include laptop {
      padding: 0;
    }
  }

  &__text {

    @include laptop {
      grid-column: 1 / 2;
      grid-row: 2;
      padding: 40px;
      max-width: 690px;
      margin-left: auto;
    }

    @include desktop {
      padding: 100px;
    }

    @media (min-width: 1600px) {
      margin-right: -180px;
    }

    @media (min-width: 1920px) {
      margin-right: -250px;
    }

    .link-more {
      margin-bottom: 43px;

      @include laptop {
        margin-bottom: 0;
      }
    }
  }

  .cases-slider {
    padding-top: 24px;
    padding-right: 23px;
    overflow: visible;
    padding-bottom: 63px;
    max-width: 1920px;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: -50vw;
      margin-left: calc(50%);
      width: 100vw;
      height: calc(((100vw * 214 / 393) / 2) + 125px);
      background-color: $color-muted;

      @include laptop {
        height: 100%;
      }
    }

    @include laptop {
      padding: 0;
    }

    .swiper-wrapper {

      @include laptop {
        display: grid;
        grid-template-columns: 1fr 1fr;
      }
    }

    .swiper-slide {
      &:nth-child(3) {
        .card-cases {
          @include laptop {
            max-width: 676px;
            margin-left: auto;
          }
        }
      }
      .card-cases {

        @include laptop {
          height: calc(100vw * 640 / 1920);
          max-height: 640px;
          max-width: 960px;
        }

        &__img {
          margin-bottom: 23px;
          height: calc(100vw * 214 / 393);

          @include laptop {
            margin-bottom: 0;
            height: 100%;
          }
        }
      }


    }

    .swiper-pagination {
      top: 0;
      right: 23px;
      bottom: auto;
      left: auto;
      text-align: right;

      @include laptop {
        display: none;
      }

      .swiper-pagination-bullet {
        vertical-align: top;
        background-color: $color-secondary;
        opacity: 1;
      }

      .swiper-pagination-bullet-active {
        background-color: $color-primary;
      }
    }
  }
}
